import React from "react";
import { Link } from "react-router-dom";

const Pharmacy = () => {
  return (
    <div
      style={{
        minHeight: "100vh",
        // background: "linear-gradient(135deg, #a8edea 0%, #fed6e3 100%)",
        //    background: 'linear-gradient(135deg, #e2f0fc, #fbe6e0, #f9e9cc, #ecf1e6, #f1e9f2)',
        backgroundImage: "url('./assets/Background.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        fontFamily: "Arial, sans-serif",
        padding: "20px",
      }}
    >
      <div
        style={{
          textAlign: "center",
          maxWidth: "800px",
          width: "100%",
        }}
      >
        <h1
          style={{
            fontSize: "3rem",
            fontWeight: "bold",
            marginBottom: "20px",
            color: "#333",
            lineHeight: "1.2",
          }}
        >
          Great things coming soon.
        </h1>

        <p
          style={{
            fontSize: "1.2rem",
            color: "#666",
            marginBottom: "40px",
            lineHeight: "1.5",
          }}
        >
          We are working hard to bring you the best online pharmacy experience.
        </p>

        <Link
          to="/"
          style={{
            display: "inline-block",
            padding: "12px 30px",
            backgroundColor: "transparent",
            color: "#333",
            border: "2px solid #333",
            borderRadius: "10px",
            textDecoration: "none",
            fontSize: "1rem",
            fontWeight: "500",
            transition: "all 0.3s ease",
          }}
          onMouseOver={(e) => {
            e.target.style.backgroundColor = "#333";
            e.target.style.color = "white";
          }}
          onMouseOut={(e) => {
            e.target.style.backgroundColor = "transparent";
            e.target.style.color = "#333";
          }}
        >
          Go Home
        </Link>
      </div>
    </div>
  );
};

export default Pharmacy;
